<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
export default {
  name: "HomePage",

  data() {
    return {
      currentPage: 1,
      delay: true,
      pageCount: 3,
      isMove: false,
    };
  },
  async mounted() {
    const container = document.getElementById("sec1");
    let camera = new THREE.PerspectiveCamera(
      50,
      container.clientWidth / container.clientHeight,
      0.1,
      1000
    );
    camera.position.set(-126, 126, 102);

    let renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
    });
    renderer.setPixelRatio(devicePixelRatio);
    renderer.setSize(container.clientWidth, container.clientHeight);
    renderer.gammaOuput = true;
    renderer.gammaFactor = 2.2;
    let scene = new THREE.Scene();
    const loader = new GLTFLoader();
    let model;
    loader.load(
      "/assets/models/scene.gltf",
      (gltf) => {
        model = gltf.scene;
        scene.add(model);
      },
      (xhr) => {
        console.log(xhr);
      },
      (error) => console.log(error)
    );

    var light = new THREE.AmbientLight(0xffffff);
    scene.add(light);

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.autoRotate = true;
    controls.autoRotateSpeed = 0.5;
    container.appendChild(renderer.domElement);
    renderer.setAnimationLoop(() => {
      onWindowResize();
      controls.update();
      renderer.render(scene, camera);
    });
    window.addEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefox
    window.addEventListener("mousewheel", this.handleMouseWheel, {
      passive: false,
    });
    function onWindowResize() {
      camera.aspect = container.clientWidth / container.clientHeight;

      camera.updateProjectionMatrix();

      renderer.setSize(container.clientWidth, container.clientHeight);
    }

    // handler cloud
    // eslint-disable-next-line no-undef
    TagCanvas.Start('myCanvas','tags',{
            textColour: '#ff0000',
            outlineColour: '#ff00ff',
            reverse: true,
            depth: 0.8,
            maxSpeed: 0.05
          });
  },

  methods: {
    scrollToView(view) {
      const el = document.getElementById(view);
      el.scrollIntoView({ behavior: "smooth" });
      this.currentPage = 2;
    },
    handleMouseWheel: function (e) {
      console.log("handleMouseWheel");
      if (e.wheelDelta < 30 && !this.inMove) {
        // this.moveUp();
        console.log("move up");
        if (this.currentPage < this.pageCount) {
          this.currentPage++;
          console.log(this.currentPage);
          this.handleScroll(this.currentPage);
        }
      } else if (e.wheelDelta > 30 && !this.inMove) {
        // this.moveDown();
        console.log("move down");
        if (this.currentPage > 1) {
          this.currentPage--;
          console.log(this.currentPage);
          this.handleScroll(this.currentPage);
        }
      }

      e.preventDefault();
      return false;
    },
    /**
     * Handle the 'DOMMouseScroll' event for Firefox
     */
    handleMouseWheelDOM: function (e) {
      console.log("handleMouseWheelDOM");
      if (e.detail > 0 && !this.inMove) {
        // this.moveUp();
        console.log("move up");
      } else if (e.detail < 0 && !this.inMove) {
        // this.moveDown();
        console.log("move down");
      }

      return false;
    },
    handleScroll(currentPage) {
      setTimeout(function () {
        this.delay = false;
      }, 100);
      const section = document.getElementById("sec" + currentPage);
      section.scrollIntoView({ behavior: "smooth" });
    },
  },
};
</script>

